.chat_list_container {
  background-color: #fff;
  max-height: 100%;
  overflow-y: auto;
  padding: 15px 0px;
  &::-webkit-scrollbar {
    width: 7px;
  }
  &::-webkit-scrollbar-track {
    background: white;
  }
  &::-webkit-scrollbar-thumb {
    background: lightgray;
    border-radius: 10px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    padding: 0px 15px;
    margin: 15px 0px;
    b {
      color: #797c8c;
      font-size: 18px;
    }
    button {
      background-color: #4eac6e;
      color: white;
      border: none;
      padding: 3px;
      border-radius: 4px;
      transition: all 0.2s ease-in-out;
      i {
        font-size: 16px;
      }
    }
  }

  .user_card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    padding: 5px 15px;
    transition: background-color 0.2s ease-in-out;
    cursor: pointer;
    &:is(.active) {
      background-color: #4eac6e;
      .user {
        span {
          color: white;
        }
      }
      label {
        background-color: #7bbf90;
        color: white;
      }
    }
    .user {
      display: flex;
      align-items: center;
      gap: 10px;
      span {
        color: #5a636c;
        font-size: 14px;
      }
    }
    label {
      font-size: 10px;
      margin: 0px;
      background-color: #dbdbdd;
      color: #5a636c;
      padding: 3px 5px;
      border-radius: 4px;
    }
  }
}
